<template>
  <div class="all">
    <AutoSwiper :imgs="news.slice(0, 4)"></AutoSwiper>
    <div class="magazine">
      <div class="sub-header mag-bg">
        <img src="../../static/img/RUNWAY-01.png"/>
        <h3>{{config.home.runway}}</h3>
      </div>
      <div class="center" >
        <div class="mag-photo" >        
            <div v-for="(img, index) in shows.slice(0, 3)" :key="index">
                <img :src="img.img_url" alt="magzine" @click="toShow(img.id)">
                <p class="desc">{{img.desc}}</p>
            </div>  
        </div> 

      </div>      
    </div>
    <div class="runway">
      <div class="sub-header run-bg" >
        <img src="../../static/img/designer.png"/>
        <h3>{{config.home.designer}}</h3>
      </div>
      <div class="center">
        <div class="run-photo">
          <div v-for="(img, index) in designerImgs" :key="index">
            <img :src="img.img_url" alt="runway" @click="turnToDesigner(img.id)">
          </div>
        </div>
      </div>
    </div>
    <div class="news">
      <!--<div class="bg"></div>-->

      <div class="sub-header" style="position: relative;">
        <img src="../../static/img/news.png" style="width: 120px"/>
        <h3>{{config.home.news}}</h3>
      </div>

      <div class="news-photo">
        <div v-for="(item, index) in news.slice(0, 3)" :key="index" class="news-item" :style="{ backgroundImage: `url('${item.cover_img}')` }" @click="toNews(item.newsId)">
          
          <div>
            <p class="time" style="font-family: Bernard, CH; letter-spacing: 2px">{{item.time}}</p>
            <ul>
              <li v-for="(tag, index) in item.tag" :key="index" style="font-family: Bernard, CH; letter-spacing: 2px">{{tag.tagTitle}}</li>
            </ul>
          </div>
          
          <h4 style="font-family: Bernard, CH; letter-spacing: 2px">{{item.title}}</h4>
          
          
        </div>
      </div>

        <img :src="config.home.img" class="regen" @click="toRegen">
        <!--<img v-for="img in news.slice(3)" :src="img.cover_img" alt="weibo" @click="toNews(img.newsId)">-->

    </div>
    <Footer></Footer>

  </div>

</template>

<script>
  import AutoSwiper from './swipers/AutoSwiper.vue'
  import {getAllDesigners, getAllNews, getAllShows, getAllJsons} from '../service/getData'
  /* eslint-disable */
  export default {
    name: 'Home',
    data () {
      return {
        title: 'RENGENATION',
        shows: [],
        config: {
          home: {
            runway: '',
            designer: '',
            news: ''
          }
        },
        designerImgs: [
          {
            url: '../static/img/img2.jpg',
            id: 1
          },
          {
            url: '../static/img/img6.jpg',
            id: 2
          },
          {
            url: '../static/img/img7.jpg',
            id: 3
          },
          {
            url: '../static/img/img8.jpg',
            id: 4
          }
        ],
        news: [],
        startSearch: false,
        searchValue: ''
      }
    },
    methods: {
      formatDate (time) {
        let res = ''
        let date = new Date(time)
        res = `${date.getFullYear()}-${date.getMonth() + 1}-${date.getDate()}`
        return res
      },
      toRegen () {
        window.open('http://regen.org.cn/')
      },
      toShow (id) {
        this.$router.push({name: 'show', params: {showId: id}})
      },
      toNews (id) {
        this.$router.push({name: 'newsDetail', params: {newsId: id}})
      },
      test () {
        console.log('test')
      },
      start (e) {
        this.startSearch = !this.startSearch
      },
      submit (e) {
        console.log(this.searchValue)
        this.startSearch = false
        this.searchValue = ''
      },
      turnToDesigner (id) {
        this.$router.push({name: 'designerPro', params: { designerId: id }})
      },
      async initData () {
        getAllDesigners(0, 5).then(res => {
          if (res.code === '0') {
            this.designerImgs = res.msg.designers
          }
        }).catch(err => {
          console.log(err)
        })

        getAllNews(0, 9).then(res => {
          console.log(res)
          this.news = res.msg.newses
          function sortDate (new1, new2) {
            let day1 = new Date(new1.time)
            let day2 = new Date(new2.time)
            return day2.getTime() - day1.getTime()
          }
          this.news.sort(sortDate)
          this.news.forEach(n => {
            n.time = this.formatDate(n.time)
          })
        }).catch(err => {
          console.log(err)
        })

        getAllShows(0, 10).then(res => {
          console.log("all shows here")
          console.log(res)
          this.shows = res.msg.shows
        }).catch(err => {
          console.log(err)
        })

        getAllJsons().then(res => {
          console.log('config', res)
          if (res.code === '0') {
            this.config = JSON.parse(res.msg.extraTitle)
          }
        }).catch(err => {
          console.log(err)
        })

      }
    },
    components: {
      AutoSwiper
    },
    mounted () {
      console.log('ready')
      document.title = this.title
      this.initData()
    }
  }
</script>

<style scoped lang="scss">

  @font-face {
    font-family: 'CH';
    src: url(../assets/fonts/CH.otf);
  }

  .all {
    /* min-width: 1080pt; */
    padding-top: 80px;
  }

  .search > img {
    position: relative;
    width: 30px;
    height: 30px;
    transition: 1s;
    -webkit-transition: 1s;
    -moz-transition: 1s;
    -o-transition: 1s;
    cursor: pointer;
  }

  .search > input {
    display: none;
    position: absolute;
    top: 40px;
    left: 10px;
    width: 80px;
    border-width: 0 0 2px;
    background: none;
    transition: 1s;
  }

  .img-active {
    transform: translateX(-20px);
    -webkit-transform: translateX(-20px);
    -moz-transform: translateX(-20px);
    -o-transform: translateX(-20px);
  }

  .input-active {
    display: block !important;
  }

  /* magazine */
  .sub-header {
    height: 430px;
    /*background-color: white;*/
    text-align: center;
  }

  .sub-header > img {
    width: 200px;
    height: 60px;
    margin: 20px;
  }

  .sub-header > h3 {
    position: relative;
    top: -32px;
    font-size: 2em;
    margin-bottom: 15px;
  }

  .center {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: -310px;
    font-family: Bernard, CH;
    letter-spacing: 2px;
    width: 100vw;
  }

  .mag-photo {
    /*width: 100%;*/
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .mag-bg {
    background: white;
   
  }

  .mag-photo > div {
    /*margin: 0 5px*/
  }

  .mag-photo > div > img {
    width: calc(100vw - 40px);
    max-width: 540px;
    margin: 0 20px;
    cursor: pointer;
  }
  .desc{
    font-size: 15px;
    text-align: justify;
    max-width: 540px;
    padding: 20px 10%;
  }
  /* runway */
  .run-photo {
    /*width: 100%;*/
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
    cursor: pointer;
    flex-wrap: wrap;
    margin-top: -10px;
    padding: 0 20px;
    justify-content: space-between;
  }    

  .desc {
    font-size: 15pt;
  }

  .run-bg {
    background: white;
  }

  .run-photo > div {
    /*margin: 0 5px;*/
  }

  .run-photo > div > img {
    max-width: 100%;
    width: calc(50vw - 25px);
    margin: 10px 0 0;
    cursor: pointer;
  }

  /* news */
  .news-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;


  }
  .news-item {

    margin: 10px;
    min-height: 100vw;
    padding: 10px 20px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: calc(100vw - 20px);

    & > * {
      background-color: rgba(white, .5);
      padding: 5px 10px 0;

      & > * {
        margin-bottom: 5px;
      }
    }
  }

  .regen {
    width: calc(100vw - 20px);
    margin: 10px;
    box-shadow: 0px 0px 2px 0px black;
  }

  .news h4 {
    padding: 5px;
    margin: -5px;
    text-align: justify;
  }

  .news-photo {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-family: "CH";
    margin-top: -310px;
  }

  .news-first {
    position: relative;
    width: 60%;
    top: -80px;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    padding: 10px;
  }

  .news-first li {
    color: #2c3e50;
    margin: 2px;
  }

  .news-first > div {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    align-items: flex-start;
    padding-top: 20px;
  }

  .news-first > div > div:nth-child(1) {
    text-align: center;
    width: 80px;
  }

  .news-first > div > img {
    width: 250px;
    cursor: pointer;
    /*height: auto;*/
  }

  .news-first > div > div:nth-child(3) {
    width: 25%;
    text-align: left;
    padding-left: 10px;
  }

  .news-second {
    position: relative;
    width: 45%;
    top: -210px;
    left: 80px;
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 1600px;
  }

  .news-second > img {
    width: 45%;
    height: auto;
    padding: 10px;
    cursor: pointer;
  }

  .weibo-follow {
    position: absolute;
    top: 15%;
    left: 25%;
  }

  .weibo-follow > img {
    opacity: 0.7;
    cursor: pointer;
  }

  .weibo-follow > div {
    background-color: white;
    text-align: center;
    font-size: 1.5em;
    padding:20px;
  }


  .add-img {
    width: 400px !important;
    margin-top: 150px;
    margin-left: 110px;
  }

  .panel {
    display: flex;
    display: -webkit-flex;
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 10px auto;
    overflow-y: scroll;
  }

  .panel > div {
    margin: 5px;
  }

  .panel > div img {
    height: 150px;
    cursor: pointer;
    transition: .5s;
  }

  .panel > div img:hover {
    transform: scale(1.1, 1.1);
  }

  .runway {
    /* height: 490px; */
  }

  .bg {
    height: 800px;
    position: absolute;
    right: 0;
    width: 1500px;
    background: white;
  
  }

</style>



